.main{
   height: 100vh;
   display: flex;
   justify-content: center;
   background-color: black;
   /*background-image: url("https://images.unsplash.com/photo-1586399254847-02e1369bac88?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80");*/
   background-image: url("https://images.unsplash.com/photo-1558980664-769d59546b3d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80");
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
}
.container{
   width: 100%;
   display: flex;
   justify-content: space-around;
   align-items: center;
}
.welcome{
   display: flex;
   flex-direction: column;
   color: #00aeef;
   font-size: 72px;
   font-weight: bold;
}
.welcome span:first-child{
   color: #ffffff;
   font-size: 42px;
}
.myPhoto{

}


      /*RESPONSIVE*/
@media (max-width: 576px) {
   .container{
      flex-direction: column;
   }
   .welcome{
      font-size: 20px;
   }
}
@media (min-width: 768px) {
   .container {
      width: 90%;
   }
}
@media (min-width: 1200px) {
   .container {
      width: 100%;
   }
}